<template>
  <div class="login-div">
    <p class="title">账号登录</p>
    <p>
      <input type="text" placeholder="请输入用户名" v-model="user.username" />
    </p>
    <p>
      <input type="text" placeholder="请输入密码" v-model="user.password" />
    </p>
    <p class="uuid">
      <input class="Verification" type="text" v-model="user.Verification" />
      <img @click=handleChange() :src=this.user.imgcode v-if="this.user.uuid">
      
    </p>
    <p>
      <button @click="submit">登录</button>
    </p>
    <p class="help">
      <span @click="hanleClick()">注册账号</span>
      <router-link tag="span" to="/registpassword">修改密码</router-link>
    </p>
  </div>
</template>

<script>
import {loginApi , uuidApi} from "../../api/login.js"
export default {
  name: "Login",
  data() {
    return {
      user: {
        username: "",
        password: "",
        Verification: "",
        uuid:"",
        imgcode:"http://192.168.130.178:8080/image/c.png"
        
      }
    };
  },
  methods: {
    hanleClick() {
      this.$emit("handle", "Regist");
    },
    async submit(){
    let data2 = await loginApi(this.user.username,this.user.password,this.user.Verification,this.user.uuid)
      if(data2==0){
        window.alert("登录成功")
      
        this.$router.push({path:'home'})
      }else if(data2==1){
        window.alert("验证码错误")
        this.$router.go(0);
      }else if(data2==2){
        window.alert("用户不存在")
        this.$router.go(0);
      }else{
        window.alert("密码错误")
        this.$router.go(0);
      }
    },
    handleChange(){
     this.$router.go(0)
    }
  },
  async created(){
    let data = await uuidApi()
    this.user.uuid = data;
   }
};
</script>

