import http from "../utils/requert";

export const registApi = (us,pw,number)=> http({
    method:"post",
    url:"/api/user/regist",
    data:{
        username:us,
        password:pw,
        phone:number
    }
})

export const loginApi = (us,pw,uid,img)=> http({
    method:"post",
    url:"/api/user/login",
    data:{
        username:us,
        password:pw,
        uuid:img,
        image:uid
    }
})

export const findallApi = ()=> http({
    method:"get",
    url:"/api/user/findAll"
})

export const uuidApi = ()=> http({
    method:"get",
    url:"/api/user/uuid"
})