import http from "../utils/requert";

export const timetrafficApi = (dur,flg,time)=>http({
    method:"post",
    url:"/api/road/getAll",
    data:{
        duration:dur,
        flag:flg,
        now:time
    }
})

export const historyrodeApi = (time,se)=>http({
    method:"post",
    url:"/api/road/getHistoryRoad",
    data:{
        now:time,
        select:se
    }
})

export const froecastnowApi = (time)=>http({
    method:"get",
    url:"/api/getPredictNow",
    data:{
        now:time
    }
})

export const froecastApi = (time)=>http({
    method:"post",
    url:"/api/getPredict",
    data:{
        now:time
    }
})

export const froecasthistoryApi = (time)=>http({
    method:"get",
    url:"/api/getPredictHistory",
    data:{
        now:time
    }
})