<template>
  <div class="swipe-box">
    <div class="head">
      <span>实时路况</span>
      [{{this.date}}]
    </div>
    <div class="main">
      <label >仅显示拥堵路段</label><input type="checkbox" value="1" name="demand" v-model="demand">
    </div>
    <div class="tab">
      <div>区域路段</div>
      <div>车速</div>
      <div>流量</div>
      <div>指数</div>
      <div>时长</div>
      <div>长度</div>
    </div>
    <swiper :options="swiperOption" class="swiper" v-if="demand">
      <div v-for="(item,index) in this.msg" class="detail" v-show="item.speed<60" :class="{colorborder:item.speed<20}" style="border: 1px solid yellow;cursor: pointer;" @click="handleInfo(item)">
        <span>{{item.roadName}}</span>
        <span>{{item.speed}}</span>
        <span>{{item.volume}}</span>
        <span>{{item.index}}</span>
        <span>{{item.duration}}</span>
        <span>1km</span>
      </div>
    </swiper><swiper :options="swiperOption" class="swiper" v-else>
      <div class="swiper-slide detail" v-for="(item,index) in this.msg" :class="{colorborder:item.speed<20,colorbordery:item.speed<60 && item.speed>=20}" style="cursor: pointer;" @click="handleInfo(item)">
        <span>{{item.roadName}}</span>
        <span>{{item.speed}}</span>
        <span>{{item.volume}}</span>
        <span>{{item.index}}</span>
        <span>{{item.duration}}</span>
        <span>1km</span>
      </div>
    </swiper>
  </div>
</template>

<script>
  import "../../assets/swiper.min.css";

  export default {
    name: "Swipe",
    props: ["msg"],
    data() {
      return {
        swiperOption: {
          direction: "vertical", // 垂直切换选项
          pagination: {
            el: ".swiper-pagination",
            clickable: true // 允许点击小圆点跳转
          },
          autoplay: {
            delay: 3000,
            disableOnInteraction: false // 手动切换之后继续自动轮播
          },
          loop: true,
          slidesPerView: "auto",
          navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev"
          }
        },
       
        date: "",
        demand :'',
        info:""
      };
    },
    mounted() {
      let date = new Date();
      let h = date.getHours().toString().length <= 1 ?('0' + date.getHours()) : date.getHours();
      let d = date.getMinutes().toString().length <= 1 ?('0' + date.getMinutes()) : date.getMinutes();
      this.date = h + ":" + d
    },
    watch: {
      demand:{
        handler(newV,oldV){
        }
      }
    },
    methods: {
      handleInfo(info){
        this.info=info;
        this.$emit("handle",this.info)
      }
    },
    //定义swiper对象
  };
</script>

