import http from "../utils/requert";

export const datanumApi = (time="9-18") => http({
    method:"get",
    url:"/api/report/getCount",
    data:{
        hour:time
    }
})

export const delayApi = (time="9-18") =>http({
    method:"get",
    url:"/api/report/getDelay",
    data:{
        time:time
    }
})

export const delayavgApi =(time="9-18")=>http({
    method:"",
    url:"/api/report/getAvg",
    data:{
        hour:time
    }
})

export const historyApi = (hour="9-18",a201,rode) =>http({
    method:"post",
    url:"/api/report/getHistory",
    data:{
        hours:hour,
        road:a201,
        a01:rode
    }
})

export const froecastApi = (time,rode,a01)=>http({
    method:"post",
    url:"/api/getPredict",
    data:{
        hours:time,
        road:rode,
        a01:a01
    }
})
