<template>
  <div class="login">
    <Login v-if="this.params=='Login'" @handle="hanledataRegist" />
    <Regist v-else @handle="handleLogin"></Regist>
  </div>
</template>

<script>
import Login from "../../components/login";
import Regist from "../../components/regist";
export default {
  data() {
    return {
      params: "Login"
    };
  },
  components: {
    Login,
    Regist
  },
  methods: {
    hanledataRegist(params) {
      this.params = params;
    },
    handleLogin(params) {
      this.params = params;
    }
  }
};
</script>

<style lang="scss" scoped>
.login {
  width: 100%;
  height: 100%;
  background: url("../../assets/1.png") no-repeat center center;
  background-size: 100% 100%;
}
</style>