import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/login'
import Registpw from "../views/registpassword"
import Home from "../views/home"
import Reportform from "../views/reportform"
import Forecast from "../views/forecast"

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login
  },
  {
    path: '/registpassword',
    name: "Registpw",
    component:Registpw
  },
  {
    path:'/home',
    name:"Home",
    component:Home,
    meta:{
      flag:true
    }
  },
  {
    path:"/reportform",
    name:"Reportform",
    component:Reportform,
    meta:{
      flag:true
    }
  },{
    path:"/forecast",
    name:"Forecast",
    component:Forecast,
    meta:{
      flag:true
    }
  }
]

const router = new VueRouter({
  // mode: 'history',
  // base: process.env.BASE_URL,
  routes
})

export default router
