<template>
    <div class="head">
        <div class="head-l">
          <img src="../../assets/logo.png" alt />
          <div>
            <p>广西智慧高速应用大数据平台</p>
          </div>
          <div
            class="nav-head"
            :class="{nav:k==index}"
            @click="handleClick(item.path,index)"
            v-for="(item,index) in navhead"
            :key="index"
          >
            <img :src="item.img" />
            {{item.title}}
          </div>
        </div>
        <div class="head-r">
          <p>{{this.date}}</p>欢迎您 广西智慧高速
        </div>
      </div>
</template>

<script>
export default{
    name:"Head",
    data() {
        return {
            navhead: [
        { img: require("../../assets/nav.png"), title: "实时路况" ,path:"/home"},
        { img: require("../../assets/nav2.png"), title: "预置点预测",path:"/forecast" },
        { img: require("../../assets/nav3.png"), title: "报表分析" ,path:"/reportform"}
      ],
      k: 0, 
      date:""
        }
    },
    created() {
        let date = new Date();
    this.date =date.toLocaleString().slice(0,8)+" "+date.getHours()+":"+date.getMinutes()
    this.k=window.localStorage.getItem("k")
    window.localStorage.removeItem('k')
    },
    mounted() {
      window.addEventListener("beforeunload",()=>{
        window.localStorage.setItem("k",this.k)
        
    })
    },
    methods: {
        handleClick(path,index){
            this.k=index;
           this.$router.push(path)
          
        }
    },
    beforeDestroy() {
     
    },
}
</script>

