<template>
    <div class="datanum">
        <div class="block" style="text-align: left;">
            <el-date-picker v-model="value1" type="datetimerange" range-separator="至" start-placeholder="开始日期"
                end-placeholder="结束日期" value-format="yyyy-MM-dd HH" format="yyyy-MM-dd HH:mm">
            </el-date-picker>
            <div class="btn" @click="handleSearch">查询</div>
        </div>
        <div id="echars">

        </div>
        <div class="table">
            <table>
                <tr>
                    <th>时间</th>
                    <th>信令数量</th>
                </tr>
                <tr v-for="(item,index) in data" :key=index>
                    <td>{{date}} {{item.hour}}</td>
                    <td>{{item.count}}</td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
    import { datanumApi} from "../../api/reportform.js"
    export default {
        name: "Datanum",
        data() {
            return {
                data: "",
                time: "9-18",
                hourlist: [],
                countlist: [],
                pickerOptions: {
                    shortcuts: [{
                        text: '最近一周',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近一个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近三个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },
                value1: [new Date(new Date().getFullYear(),new Date().getMonth(),new Date().getDate(),9,0), new Date(new Date().getFullYear(),new Date().getMonth(),new Date().getDate(),18,0)],
                value2: '',
                date: ""
            }
        },
        watch: {
            hourlist: {
                handler(newVal, oldVal) {
                    this.drawChart()
                },
                deep: true
            },
        },
        async created() {
            let date = new Date();
            this.date=date.getFullYear() +"-" +date.getMonth() +"-"+date.getDay()
            let data = await datanumApi(this.time)
            this.data = data;
            for (let i = 0; i < data.length; i++) {
                this.hourlist.push(data[i].hour)
                this.countlist.push(data[i].count)
            }

        },
        mounted() {

        },
        methods: {
            drawChart() {
                let myChart = this.$echarts.init(document.getElementById("echars"));
                myChart.resize()
                var option = {
                    title: {
                        text: "信令数据量"
                    },
                    legend: {
                        left: 'right',
                        orient:'vertical'
                    },
                    tooltip: {},
                    xAxis: {
                        data: this.hourlist
                    },
                    yAxis: {},
                    series: [{
                        name: '信令数量',
                        type: 'line',
                        data: this.countlist
                    }]
                };
                // 使用刚指定的配置项和数据显示图表。
                myChart.setOption(option)
            },
            async handleSearch() {
                this.value2 = ""
                for (let i = 0; i < this.value1.length; i++) {
                    this.value2 += this.value1[i].slice(-2) + "-"
                }
                this.value2 = this.value2.slice(0, -1)
                let data = await datanumApi(this.value2)
                this.data = data;
                this.hourlist = [];
                this.countlist = [];
                for (let i = 0; i < data.length; i++) {
                    this.hourlist.push(data[i].hour)
                    this.countlist.push(data[i].count)
                }
                
            }
        },
    }
</script>

