<template>
    <div class="swipe-box">
      <div class="head">
        <span>预置点</span></span>
        [{{this.date}}]
      </div>
      <div class="tab">
        <div>预置点名称</div>
        <div>方向</div>
        <div>车速</div>
        <div>流量</div>
      </div>
      <div class="swiper" >
        <swiper :options="swiperOption" class="swiper-main">
           
            <div class="swiper-slide detail" v-for="(item,index) in data" :key="index" @click="handleInfo(item)" style="cursor: pointer;;">
              <span>{{item.roadName}}</span>
              <span>{{item.a01}}</span>
              <span>{{item.speed}}</span>
              <span>{{item.volume}}</span>
            </div>
          </swiper>
      </div>
    </div>
  </template>
  
  <script>
    import "../../assets/swiper.min.css";
    import {froecastnowApi} from "../../api/timerode"
    export default {
      name: "Swipe",
      props: ["msg"],
      data() {
        return {
          swiperOption: {
            direction: "vertical", // 垂直切换选项
          pagination: {
            el: ".swiper-pagination",
            clickable: false // 允许点击小圆点跳转
          },
          autoplay: {
            delay: 3000,
            disableOnInteraction: true // 手动切换之后继续自动轮播
          },
          loop:true,
          slidesPerView: "auto",
          navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev"
          }
          },
         
          date: "",
          demand :'',
          info:"",
          data:""
        };
      },
      mounted() {
        let date = new Date();
        let h = date.getHours().toString().length <= 1 ?('0' + date.getHours()) : date.getHours();
        let d = date.getMinutes().toString().length <= 1 ?('0' + date.getMinutes()) : date.getMinutes();
        this.date = h + ":" + d
      },
      async created() {
      let date=new Date()
      let d=date.getMinutes().toString().length <= 1 ? ('0' + date.getMinutes()) : date.getMinutes();
      let time=date.getHours()+":"+d
      let data=await froecastnowApi(time)
     this.data=data

    },
      watch: {
        demand:{
          handler(newV,oldV){
          }
        }
      },
      methods: {
        handleInfo(info){
          this.info=info;
          this.$emit("handle",this.info)
          console.log(info)
        }
      },
      //定义swiper对象
    };
  </script>
  
  <style lang="scss" scoped>
    * {
      margin: 0;
      padding: 0;
    }
  
    .swipe-box {
      width: 25%;
      height: 75%;
      position: absolute;
      top: 20%;
      right: 6%;
    }
  
    
  
    .head {
      background: #0c74b6;
      color: #f7f7f8;
      width: 100%;
      height: 10%;
      font-size: 16px;
      line-height: 60px;
      font-weight: bold;
      display: flex;
      justify-content: flex-start;
    }
  
    .head span {
      margin: 0 130px 0 30px;
    }
  
    .tab {
      display: flex;
      justify-content: space-between;
      background: #fff;
    }
  
    .tab div {
      background: #0c74b6;
      color: #f7f7f8;
      padding: 0 7px;
      margin: 10px 15px;
      font-size: 16px;
    }
    .swiper {
      width: 100%;
      height: 60%;
      background: #fff;
    }
    .swiper-main {
      width: 100%;
      height: 170px;
    
    }
    .detail {
        width: 92%;
      display: flex;
      justify-content: space-between;
      border: 1px solid green;
      height: 30px !important;
      margin-top: 5px;
      align-items: center;
      margin: 5px auto;
    }
  
    .detail span {
      margin: 10px 10px 10px 5px;
      font-size: 16px;
    }
  
    .main {
      background: #fff;
      font-size: 16px;
      height: 36px;
      line-height: 36px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      cursor: pointer;
    }
  
    .main input {
      margin-left: 5px;
      width: 16px;
      height: 16px;
    }
    .colorborder{
      border: 1px solid red !important;
    }.colorbordery{
      border: 1px solid yellow !important;
    }
  </style>