<template>
  <div class="login-div">
    <p class="title">注册账号</p>
    <p>
      <input type="text" placeholder="请输入用户名" v-model="user.username" />
    </p>
    <p>
      <input type="text" placeholder="请输入密码" v-model="user.password" />
    </p>
    <p>
      <input class="Verification" placeholder="请输入手机号" type="text" v-model="user.phone" />
    </p>
    <p>
      <button @click="handleregist">注册</button>
    </p>
    <p class="help">
      <span @click="handleclick">账号登录</span>
    </p>
  </div>
</template>

<script>
import {registApi} from "../../api/login.js";
export default {
  name: "Regist",
  data() {
    return {
      user: {
        username: "",
        password: "",
        phone: ""
      }
    };
  },
  methods: {
    handleclick() {
      this.$emit("handle", "Login");
    },
    async handleregist() {
      let data = await registApi(this.user.username,this.user.password,this.user.phone);
      console.log(data)
      if (data == 0) {
        window.alert("注册成功");
        this.$emit("handle","Login")
      } else {
        window.alert("用户存在");
        this.user.username=''
        this.user.password=''
        this.user.phone=''
      }
    }
  },
  
};
</script>

