<template>
    <div class="forecastform">
        <div class="block" style="text-align: left;">
            <el-date-picker v-model="value1" type="datetimerange" range-separator="至" start-placeholder="开始日期"
                end-placeholder="结束日期" value-format="HH" format="yyyy-MM-dd HH:mm">
            </el-date-picker>
            <div>
                <el-select v-model="value" placeholder="请选择">
                    <el-option v-for="item in rodename" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
            </div>
            <div>
                <el-select v-model="value0" placeholder="请选择">
                    <el-option v-for="item in direction" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
            </div>

            <div class="btn" @click="handleSearch">查询</div>
        </div>
        <div id="echars">

        </div>
        <div class="table">
            <table>
                <tr>
                    <th>时间</th>
                    <th>预置点</th>
                    <th>方向</th>
                    <th>预测流量</th>
                    <th>实际流量</th>
                </tr>
                <tr v-for="(item,index) in data" :key="index">
                    <td>{{item.time}}</td>
                    <td>{{item.roadName}}</td>
                    <td>{{item.a01}}</td>
                    <td>{{item.predictVolume}}</td>
                    <td>{{item.volume}}</td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
    import { froecastApi } from "../../api/reportform"
    export default {
        name: "Forecastform",
        data() {
            return {
                value1: [new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate(), new Date().getHours() - 1, 0), new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate(), new Date().getHours(), 0)],
                value2: '',
                date: "",
                rodename: [{
                    value: 'K1260',
                    label: 'K1260互通'
                }, {
                    value: 'K1273',
                    label: 'K1273互通'
                }],
                value: 'K1260',
                direction: [{
                    value: "1",
                    label: "柳州"
                }, {
                    value: "0",
                    label: "南宁"
                }],
                value0: "1",
                rode: "K1260",
                data: "",
                hour: "",
                hour1: "",
                volum: [],
                predictVolume: [],
                timelist: []
            }
        },
        async created() {
            this.drawChart()
            let date = new Date()
            let h1 = date.getHours() - 1
            this.hour1 = h1.toString()
            this.hour = date.getHours()
            let h = h1.toString() + "-" + date.getHours()
            let data = await froecastApi(h, this.rode, this.value0)
            this.data = data

            for (let i = 0; i < data.length; i++) {
                this.volum.push(data[i].volume)
                this.predictVolume.push(data[i].predictVolume)
                this.timelist.push(data[i].time)


            }
        },
        watch: {
            volum: {
                handler(newVal, oldVal) {
                    this.drawChart()
                },
                deep: true
            },
            value1: {
                handler(newVal, oldVal) {
                    this.value2 = ''
                    for (let i = 0; i < this.value1.length; i++) {
                        this.value2 += this.value1[i] + "-"
                    }
                    this.value2 = this.value2.slice(0, -1)

                },
                deep: true
            },
        },
        mounted() {
            this.drawChart()

        },


        methods: {
            drawChart() {
                let myChart = this.$echarts.init(document.getElementById("echars"));
                myChart.resize()
                var option = {
                    title: {
                        text: "柳州互通南宁方向流量预测分布"
                    },
                    legend: {
                        left: 'right',
                        orient:'vertical'
                    },
                    tooltip: {
                        show: true
                    },
                    xAxis: {
                        data: this.timelist,

                    },
                    yAxis: { splitLine: { show: false }, },
                    series: [{
                        name: '实际流量',
                        type: 'line',
                        data: this.volum,

                    }, {
                        name: '预计流量',
                        type: 'line',
                        data: this.predictVolume,
                        lineStyle: {
                            normal: {
                                color: 'green',
                                width: 2,
                                type: 'dashed'
                            }
                        },
                    }],

                };
                // 使用刚指定的配置项和数据显示图表。
                myChart.setOption(option)
            },
            async handleSearch() {
                if (this.value2 == '') {
                    let date = new Date()
                    let h1 = date.getHours() - 1
                    let h = h1.toString() + "-" + date.getHours()
                    this.value2 = h

                }
                console.log(this.value2)
                let data = await froecastApi(this.value2, this.value, this.value0)
                this.data = data
                this.volum = []
                this.predictVolume = []
                this.timelist = []
                for (let i = 0; i < data.length; i++) {
                    this.timelist.push(data[i].time)
                    this.volum.push(data[i].volume)
                    this.predictVolume.push(data[i].predictVolume)

                }
            }
        },
    }
</script>

<style lang="scss" scoped>
    * {
        margin: 0;
        padding: 0;
        border: none;
    }

    .forecastform {
        width: 100%-17%;
        position: relative;
        left: 17%;
        top: 8%;
    }

    .block {
        width: 100%-17%;
        display: flex;
    }

    .btn {
        width: 6%;
        height: 26px;
        ;
        background: #0c74b6;
        color: #f7f7f8;
        font-size: 16px;
        text-align: center;
        line-height: 26px;
        margin-left: 30px;
        border-radius: 0.2rem;
        cursor: pointer;
    }

    #echars {
        width: 100%;
        height: 500px;
        margin: 0;
    }

    .table {
        width: 100%;
        padding-bottom: 100px;
    }

    .table table {
        width: 100%;
        border: 1px solid #ddd;
        border-spacing: 0;
        border-collapse: collapse;

    }

    .table tr {
        height: 30px;
        font-size: 16px;
    }

    .table th {
        width: 20%;
        height: 50px;
        font-size: 19px;
        background: #f4f7f9;
        color: #93969c;
        border-right: 1px solid #ddd;
    }

    .table td {
        border: 1px solid #ddd;

    }
</style>