<template>
    <div class="lishi">
        <div class="tit">
            <span></span>预置点历史流量 </div>

        <div>
            <el-select v-model="value0" placeholder="请选择" class="xuanze">
                <el-option v-for="item in direction" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
            </el-select>
        </div>
        <div id="echars">

        </div>
    </div>
</template>

<script>
    import { froecasthistoryApi } from "../../api/timerode"
    export default {
        name: "Historyforecast",
        data() {
            return {
                direction: [{
                    value: "1",
                    label: "柳州"
                }, {
                    value: "0",
                    label: "南宁"
                }],
                value0: "1",
                up: [],
                down: [],
                timelist: []
            }
        },
        async created() {
            let date = new Date()
            let d = date.getMinutes().toString().length <= 1 ? ('0' + date.getMinutes()) : date.getMinutes();
            let time = date.getHours() + ":" + d + "_1"
            let data = await froecasthistoryApi(time)
            for (let i = 0; i < data.length; i++) {
                if (data[i].a01 == "0") {
                    this.up.push(data[i].volume)

                } else {
                    this.down.push(data[i].volume)
                    this.timelist.push(data[i].time)
                }
            }

        },
        watch: {
            up: {
                handler(newVal, oldVal) {
                    this.drawChart()
                },
                deep: true
            },
            value0: {
                async handler(newVal, oldVal) {
                    let date = new Date()
                    let d = date.getMinutes().toString().length <= 1 ? ('0' + date.getMinutes()) : date.getMinutes();
                    let time = date.getHours() + ":" + d + "_" + this.value0
                    let data = await froecasthistoryApi(time)
                    this.up = []
                    this.down = []
                    this.timelist = []
                    for (let i = 0; i < data.length; i++) {
                        if (data[i].a01 == "0") {
                            this.up.push(data[i].volume)

                        } else {
                            this.down.push(data[i].volume)
                            this.timelist.push(data[i].time)
                        }
                    }
                },
                deep: true
            },

        },
        mounted() {
            this.drawChart()
        },
        methods: {
            drawChart() {
                let myChart = this.$echarts.init(document.getElementById("echars"));
                myChart.resize()
                var option = {
                    axisTick: {
                        show: false
                    },
                    legend: {
                        left: 'right',
                      
                    },
                    xAxis: {
                        type: 'category',
                        data: this.timelist
                    },
                    yAxis: {
                        type: 'value',
                        splitLine: { show: false },
                    },
                    grid:{
                        x:'20px',
                        y:'15px',
                        x2:'10px',
                        y2:'20px'
                    },
                    series: [{
                        data: this.up,
                        type: 'line',
                        name:"柳州",
                        lineStyle: {
                            normal: {
                                color: 'blue',
                                width: 2,
                                
                            }
                        },
                    }, {
                        data: this.down,
                        type: 'line',
                        name:"南宁",
                        lineStyle: {
                            normal: {
                                color: 'red',
                                width: 2,
                               
                            }
                        },
                    }]
                };
                // 使用刚指定的配置项和数据显示图表。
                myChart.setOption(option)
            },
        },
    }
</script>

<style lang="scss" scoped>
    .lishi {
        width: 22%;
        height: 36%;
        background: #fff;
        position: absolute;
        top: 55%;

    }

    .tit {
        width: 100%;
        height: 40px;
        line-height: 40px;
        text-align: left;
        background: #0066b8;
        box-sizing: border-box;
        color: #fff;
        padding-left: 10px;
        font-size: 16px;
        font-weight: bold;
    }

    .tit span {
        border-left: 3px solid #fff;
        padding-right: 10px;
    }

    .xuanze {
        width: 94%;
        height: 40px;
        margin: 10px 0;
    }

    #echars {
        width: 98%;
        height: 60%;
    }
</style>