               default :{
              
                var polyline = new AMap.Polyline({
              path: patharr,
              isOutline: true,
              outlineColor: "green",
              borderWeight: 0,
              strokeColor: "green",
              strokeOpacity: 1,
              strokeWeight: 4,
              // 折线样式还支持 'dashed'
              strokeStyle: "deshed",
              // strokeStyle是dashed时有效
              strokeDasharray: [10, 5],
              lineJoin: "round",
              lineCap: "round",
              zIndex: 100,
              geodesic: true
            });
            polyline.setMap(map);
            break;
              }
              case this.data[k].speed<60 && this.data[k].speed>=20:{
                var polyline = new AMap.Polyline({
              path: patharr,
              isOutline: true,
              outlineColor: "yellow",
              borderWeight: 0,
              strokeColor: "yellow",
              strokeOpacity: 1,
              strokeWeight: 4,
              // 折线样式还支持 'dashed'
              strokeStyle: "deshed",
              // strokeStyle是dashed时有效
              strokeDasharray: [10, 5],
              lineJoin: "round",
              lineCap: "round",
              zIndex: 100,
              geodesic: true
            });
            polyline.setMap(map);
            break;
              }
              case this.data[k].speed<20:{
                var polyline = new AMap.Polyline({
              path: patharr,
              isOutline: true,
              outlineColor: "red",
              borderWeight: 0,
              strokeColor: "red",
              strokeOpacity: 1,
              strokeWeight: 4,
              // 折线样式还支持 'dashed'
              strokeStyle: "deshed",
              // strokeStyle是dashed时有效
              strokeDasharray: [10, 5],
              lineJoin: "round",
              lineCap: "round",
              zIndex: 100,
              geodesic: true
            });
            polyline.setMap(map);
            break;
              }
             
            }
           
           if(k<19){
             k+=1
           }else if(k==19){
             k=k+j
             k-=2;
           }else{
             k-=1;
            
           }
           
            // 缩放地图到合适的视野级别
            map.setFitView([polyline]);
            
            patharr = [];
          }
        }
        map.setFitView([polyline]);
            map.setCenter([109.480544	,24.265348]);
            map.setZoom(14);
      }
    }
  };
</script>


